.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.frameRaid:hover {
  transform: scale(1.05);
  transition: all .3s;
}
.frameRaid {
  /*linear-gradient(357deg, rgba(246,246,246,1) 0%, rgba(198,198,198,1) 100%);*/
  max-width: 325px;
  border-radius: 0.4rem;
  color: $color-black;
  box-shadow: 0 0 6px 0 rgba(157, 96, 212, 0.5);
  border: solid 4px transparent;
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(170deg, rgb(255 240 184) 0%, rgb(159 138 63));
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 1px #fff inset;
  padding: 0;
  box-shadow: 2px 2px 8px 4px rgba(0, 0, 0, 0.25);
  position: relative;
}
.raidName{
  position: absolute;
  left: 0;
  top: 60%;
  width: 80%;
  color: #fff;
  padding-left: 10px;
  background-image: linear-gradient(90deg, rgba(191, 181, 69, 0.8) 0%, rgba(166, 166, 166, 0.3) 100%);
}
.frameR {
  /*linear-gradient(357deg, rgba(246,246,246,1) 0%, rgba(198,198,198,1) 100%);*/
  border-radius: 0.4rem;
  color: $color-black;
  box-shadow: 0 0 6px 0 rgba(157, 96, 212, 0.5);
  border: solid 2px transparent;
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(200deg, rgba(246,246,246,1) 0%, rgba(198,198,198,1));
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 1px #fff inset;
}
.frameMap {
  max-width: 330px;
  padding-top: 10px;
  border-radius: 0.4rem;
  color: $color-black;
  box-shadow: 0 0 6px 0 rgba(157, 96, 212, 0.5);
  border: solid 2px transparent;
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(200deg, rgba(246,246,246,1) 0%, rgba(198,198,198,1));
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 1px #fff inset;
  position: relative;
}
.frameMap:hover {
  transform: scale(1.05);
  transition: all .3s;
}
.frameM{
  border-radius: 0.2rem;
  border: solid 1px #dddddd1f;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.equipmentFrame{
  -moz-box-shadow:    inset 0 0 5px #666;
  -webkit-box-shadow: inset 0 0 5px #666;
  box-shadow:         inset 0 0 5px #666;
}
.buttonEquipment {
  height: 150px;
  width: 100px;
  padding: 0;
  border-radius: 0.4rem;
  color: $color-black;
  box-shadow: 0 0 6px 0 rgba(157, 96, 212, 0.5);
  border: solid 2px transparent;
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(10deg, rgba(246,246,246,1) 0%, rgba(198,198,198,1));
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 1px #fff inset;
}

.button-71 {
  background-color: #2a322c;
  border: 0;
  border-radius: 56px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  outline: 0;
  padding: 5px 10px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: all .3s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-71:before {
  background-color: initial;
  background-image: linear-gradient(#fff 0, rgba(255, 255, 255, 0) 100%);
  border-radius: 125px;
  content: "";
  height: 50%;
  left: 4%;
  opacity: .5;
  position: absolute;
  top: 0;
  transition: all .3s;
  width: 92%;
}

.button-71:hover {
  box-shadow: rgba(255, 255, 255, .2) 0 3px 15px inset, rgba(0, 0, 0, .1) 0 3px 5px, rgba(0, 0, 0, .1) 0 10px 13px;
  transform: scale(1.05);
  color: #fff;
}


@media (min-width: 768px) {
  .button-71 {
    padding: 5px 14px;
  }
}
.close a{
  color: #fd7d0d;
  text-decoration: none;
}
.ab_Top{
    position: absolute;
    top: 0;
    width: 100%;
}
.ab_bottom{
    position: absolute;
    bottom: 0;
    width: 100%;
}

.Modal{
  background-color: #0000007d;
    height: 100%;
    width: 100%;
